.ContactForm {
    width: 450px;
    height: 350px;
    background-color: rgba(255, 255, 255, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    margin-bottom: 20px;
    padding: 30px;
}

.ContactForm label {
    color: rgba(17, 15, 11, 1);
    text-transform: uppercase;
}

.ContactForm #email {
    width: 350px;
    border-radius: 3px;
    border: none;
    padding: 10px;
    margin-bottom: 20px;
    margin-top: 10px;
}

.ContactForm #email:focus {
    outline: none;
}

.ContactForm #message {
    width: 350px;
    height: 350px;
    border-radius: 3px;
    border: none;
    margin-top: 10px;
    padding: 10px;
}

.ContactForm #message:focus {
    outline: none;
}

.ContactForm button {
    width: 350px;
    margin-top: 20px;
    padding: 10px;
}

@media screen and (max-width: 800px) {
    .ContactForm {
        width: 300px;
        height: 300px;
    }

    .ContactForm #email {
        width: 250px;
    }

    .ContactForm #message {
        width: 250px;
        height: 100px;
    }

    .ContactForm button {
        width: 250px;
    }

    .ContactForm label {
        font-size: 12px;
    }
}

.ContactForm-sea, .ContactForm-light {
    background-color: rgba(1, 30, 79, 0.1);
}